@charset "UTF-8";

/*하영 추가 */
.slider-container {
    position: relative;
    /* width: 400px; */
    /* 원하는 크기로 조절 가능 */
    overflow: hidden;
    border-radius: 10px;
    margin: auto;
}

.slider-wrapper {
    display: flex;
    transition: transform 0.4s ease-in-out;
    padding: 0;
    margin: 0;
}

.slider-item {
    min-width: 100%;
    list-style: none;
    text-align: center;
}

.slider-item img {
    width: 100%;
    border-radius: 10px;
}

/* Pagination */
.slider-pagination {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.dot {
    width: 10px;
    height: 10px;
    margin: 5px;
    background-color: lightgray;
    border-radius: 50%;
    cursor: pointer;
}

.dot.active {
    background-color: black;
}
/* 탭 css  */
.project-container {
    width: 100%;
    max-width: 100%;
    margin: 0 auto;
    background: #fff;
    /* padding: 20px; */
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
    transition: all 0.3s ease-in-out;
    margin-top: 3%;
  }
  
  /* 탭 버튼 컨테이너 */
.tabs {
    display: flex;
    justify-content: center;
    gap: 12px;
    margin-bottom: 16px;
    position: relative;
  }
  
  /* 탭 버튼 스타일 */
  .tab {
    flex: 1;
    padding: 14px 20px;
    font-size: 16px;
    text-align: center;
    font-weight: bold;
    color: #6c757d;
    background: linear-gradient(to right, #f8f9fa, #e9ecef);
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    position: relative;
    overflow: hidden;
  }
  
  /* 활성화된 탭 */
  .tab.active {
    color: #fff;
    background: linear-gradient(135deg, #007bff, #0056b3);
    box-shadow: 0 4px 10px rgba(0, 123, 255, 0.4);
    transform: scale(1.05);
  }
  
  /* 탭 버튼 호버 효과 */
  .tab:hover {
    color: #fff;
    background: linear-gradient(135deg, #007bff, #0056b3);
  }
  
  /* 탭 컨텐츠 */
  .tab-content {
    padding: 24px;
    background: #f8f9fa;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    animation: fadeIn 0.4s ease-in-out;
  }
  
  /* 탭 전환 애니메이션 */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* 반응형 디자인 */
  @media (max-width: 600px) {
    .tabs {
      flex-direction: column;
      gap: 8px;
    }
  
    .tab {
      width: 100%;
      padding: 12px;
      font-size: 14px;
    }
  }
/* Navigation Buttons */
.slider-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-size: 18px;
    border-radius: 50%;
}

.slider-button-prev {
    left: 10px;
    width: 40px;
}

.slider-button-next {
    right: 10px;
    width: 40px;
}

.slider-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

/* common */
#wrapper {
    position: relative;
    width: 100%;
    height: auto;
    overflow: hidden;
}

.comm_tit {
    display: table;
    position: relative;
    margin: 0 auto 3rem;
}

.comm_tit_img {
    position: absolute;
    top: 50%;
    left: -3rem;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    width: 2rem;
    height: 2rem;
    opacity: .5;
    cursor: pointer;
}

.comm_tit_img img {
    width: 100%;
}

.comm_tit_txt {
    border-bottom: 1px solid #ccc;
    font-family: Black Han Sans, sans-serif;
    font-weight: 400;
    font-size: 3rem;
    line-height: 1.5;
}

.pageup {
    display: none;
    position: fixed;
    right: 1.5rem;
    bottom: 1.5rem;
    width: 3rem;
    height: 3rem;
    border-radius: .5rem;
    background-color: #fff;
    cursor: pointer;
    opacity: .5;
    transition: all .2s;
}

.pageup:hover {
    opacity: 1;
}

.pageup.on {
    display: block;
}

.pageup img {
    width: 100%;
}

.font_red {
    color: #ed3770;
}


/* header */
header {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    background-color: transparent;
    z-index: 10;
}

.header_inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    width: 100%;
    max-width: 71.25rem;
    height: 4.5rem;
    padding: 1.25rem 2rem;
    margin: 0 auto;
    transition: all .2s;
}

.header_inner .logo {
    display: inline-block;
    font-weight: 700;
    font-size: 1.5rem;
    color: hsla(0, 0%, 100%, .7);
    line-height: 2rem;
    cursor: pointer;
    transition: all .2s;
}

.header_inner .logo:hover {
    color: #fff;
}

.header_inner .gnb {
    margin-left: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.header_inner .mobile_menu,
.header_inner .mobile_gnb {
    display: none;
}

.header_inner .gnb li {
    position: relative;
}

.header_inner .gnb li a {
    display: inline-block;
    padding: 0 1rem;
    font-weight: 700;
    font-size: 1rem;
    color: hsla(0, 0%, 100%, .7);
    line-height: 2rem;
    cursor: pointer;
    transition: all .2s;
}

.header_inner .gnb li a:hover {
    color: #fff;
}

header.on {
    background-color: #fff;
    box-shadow: 0 1px .3rem hsla(0, 0%, 80%, .8);
}

header.on .header_inner .logo {
    color: #453a33;
}

header.on .header_inner .logo:hover {
    color: #f4983a;
}

header.on .header_inner .gnb li a {
    color: #453a33;
}

header.on .header_inner .gnb li a:hover {
    color: #f4983a;
}


/* visual */
.visual {
    position: relative;
}

.visual::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: linear-gradient(180deg, rgba(72, 68, 101, .85) 0, rgba(72, 68, 101, .85) 90%), url(../assets/bg_visual.jpg) 50% no-repeat;
    background: -o-linear-gradient(top, rgba(72, 68, 101, .85) 0, rgba(72, 68, 101, .85) 90%), url(../assets/bg_visual.jpg) 50% no-repeat;
    background-size: cover;
    z-index: -1;
}

.visual_inner {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    width: 100%;
    max-width: 71.25rem;
    margin: 0 auto;
    padding: 8.5rem 2rem 4rem;
    text-align: center;
}

.visual_tit {
    font-family: Black Han Sans, sans-serif;
    font-weight: 400;
    font-size: 4rem;
    color: #fff;
    word-break: keep-all;
    line-height: 1.3;
}

.visual_divider {
    display: block;
    width: 3.25rem;
    height: 0;
    margin: 1.5rem auto;
    border: none;
    border-top: .2rem solid #f4983a;
    opacity: 1;
}

.visual_des {
    margin: 0 auto 2rem;
    font-weight: 400;
    font-size: 1.25rem;
    color: hsla(0, 0%, 100%, .75);
    line-height: 1.5;
}

.btn_more {
    display: inline-block;
    padding: 1rem 2rem;
    border: none;
    border-radius: 10rem;
    background-color: #f4983a;
    font-weight: 400;
    font-size: 1rem;
    text-decoration: none;
    color: #fff;
    line-height: 1.5;
    cursor: pointer;
    transition: all .4s;
}

.btn_more:hover {
    background: #ef7a37;
}

/* about me */
.aboutme {
