/* skills */
.skills {
    position: relative;
    background-color: #f9c51d;
}

.skills_inner {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    width: 100%;
    max-width: 71.25rem;
    padding: 4rem 2rem;
