}

.skill_info {
    display: flex;
    display: -ms-flexbox;
    flex-flow: row wrap;
    align-content: space-around;
    align-items: flex-start;
    -ms-flex-line-pack: distribute;
}

.skill_info li {
    box-sizing: border-box;
