/* certificate */
.certificate {
    position: relative;
    background-color: #222;
}

.certificate_inner {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    width: 100%;
    max-width: 71.25rem;
    padding: 4rem 2rem;
    margin: 0 auto;
}

.certificate_list {
    display: flex;
    display: -ms-flexbox;
    flex-flow: row wrap;
    justify-content: center;
    -ms-flex-pack: distribute;
}

.certificate_list li {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    width: 26rem;
    padding: 2rem;
    border-radius: 1rem;
    background-color: #f5f5f5;
    text-decoration: none;
    cursor: pointer;
    transition: all .2s;
    margin-top: 2rem;
}

.certificate_list li:nth-child(-n + 2) {
    margin-top: 0;
}

.certificate_list li:nth-child(even) {
