    }
}


/* 미디어쿼리 1000 */
@media (max-width: 1000px) {
    .comm_tit_txt {
        font-size: 2.5rem;
    }

    .header_inner .gnb {
        display: none;
    }

    .header_inner .mobile_menu {
        display: block;
        margin-left: auto;
        box-sizing: border-box;
        -webkit-box-sizing: border-box;
        width: 3.5rem;
        height: 3rem;
        padding: .5rem 0;
        margin-top: -.5rem;
        border: 1px solid rgba(0, 0, 0, .1);
        border-radius: .25rem;
        text-align: center;
        cursor: pointer;
    }

    .header_inner .mobile_gnb {
        display: block;
        position: absolute;
        top: 72px;
        left: 0;
        right: 0;
        overflow: hidden;
        box-sizing: border-box;
        -webkit-box-sizing: border-box;
        max-height: 0;
        padding-left: 2rem;
        background-color: #fff;
        transition: max-height .2s ease;
        -webkit-transition: max-height .2s ease;
        -o-transition: max-height .2s ease;
        box-shadow: 0 4px .3rem hsla(0, 0%, 85%, .8);
    }

    .header_inner .mobile_gnb.on {
        max-height: 15.75rem;
    }

    .header_inner .mobile_gnb li {
        position: relative;
    }

    .header_inner .mobile_gnb li:first-child {
        margin-top: 1rem;
    }

    .header_inner .mobile_gnb li:last-child {
        margin-bottom: 1rem;
    }

    .header_inner .mobile_gnb li a {
        display: table;
        padding: .75rem 0;
        font-weight: 700;
        font-size: 1rem;
        color: #6c757d;
        line-height: 1.25rem;
        cursor: pointer;
        transition: all .2s;
    }

    .header_inner .mobile_gnb li a:hover {
        color: #f4983a;
    }

    .visual_tit {
        font-size: 3rem;
    }

    .visual_des {
        font-size: 1rem;
    }

    .info li {
        width: 50%;
    }

    .info li:nth-child(-n + 4) {
        margin-bottom: 2rem;
    }

    .certificate_list li {
        width: 100%;
        max-width: 30rem;
    }

    .certificate_list li:nth-child(2) {
        margin-top: 2rem;
    }

    .certificate_list li:nth-child(even) {
        margin-left: 0;
    }

    .project_tit {
        font-size: 2rem;
    }

    .project_img {
        width: 90%;
        margin-right: 0;
        margin-bottom: 1rem;
    }

    .project_des {
        width: 90%;
    }

    .career_img {
        padding-right: 2rem;
    }

    .career_img .career_img_inner {
        width: 10rem;
        height: 10rem;
    }

    .career_txt {
        padding-left: 2rem;
    }
}



/* 미디어쿼리 780 */
@media (max-width: 780px) {
    .skill_info li {
        width: 14.5rem;
    }

    .skill_info li:nth-child(2) {
        margin-left: 2rem;
    }

    .skill_info li .skill_tit {
        font-size: 1.25rem;
    }

    .project_img,
    .project_des {
        width: 100%;
    }

    .modal_body_inner {
        max-width: 90%;
    }

    .career_info {
        width: 100%;
    }

    .career_info_inner {
        flex-direction: column;
    }

    .career_img {
        padding-right: 0;
        padding-bottom: 2rem;
        border-right: none;
    }

    .career_img .career_img_inner {
        width: 15rem;
        height: 15rem;
        margin: 0 auto;
    }

    .career_txt {
        padding-left: 0;
    }
}


/* 미디어쿼리 580 */
@media (max-width: 580px) {
    .comm_tit_txt {
        font-size: 2rem;
    }

    .info li {
        width: 100%;
    }

    .info li:nth-child(-n + 5) {
        margin-bottom: 2rem;
    }

    .info_inner .img {
        margin-right: 3rem;
    }

    .skill_info li {
        width: 18rem;
    }

    .skill_info li:nth-child(2) {
        margin-left: auto;
    }

    .skill_info li .skill_tit {
        font-size: 1.5rem;
    }

    .certificate_list li {
        padding: 1.5rem;
    }

    .certificate_list li h3 {
        font-size: 1.25rem;
    }

    .project_list>li {
        padding: 2rem;
    }

    .project_tit {
        font-size: 1.8rem;
    }

    .project_txt2_1 {
        display: table;
        width: auto;
        padding-left: .5rem;
        margin-bottom: .5rem;
        border-left: 5px solid #222;
    }

    .project_txt2_1::before {
        content: "";
        display: inline;
        padding-right: 0;
    }

    .project_txt2_2 {
        width: 100%;
    }

    .modal_body_inner {
        max-width: 100%;
    }

    .career_img .career_img_inner {
        width: 12.5rem;
        height: 12.5rem;
    }
}